<template>
    <div >
        <h3 class="lisent" @click="itemClick">点击收听</h3>
        <div class="music">
            
          <div class="music-play">
            <i class="shine" v-show="isTabFixed"></i>
            <div class="cd" @click="itemClick"></div>
            <transition name="cd-play"  v-on:after-leave="afterLeave" :after-leave="afterLeave" @before-enter="beforeEnter">
              <div class="stick" v-if="flag" @click="itemClick"></div>
            </transition>  
            <div class="stick1" v-show="isTabFixed"></div>
          </div>
          <div class="music-control">
                <div class="left">
                  <img src="~assets/img/home/btn_prev.png" alt="">
                </div>
                <div class="center" @click="itemClick">
                  <img :src="flag==true ? require('../../../assets/img/home/btn_play.png') : require('../../../assets/img/home/btn_pause.png')"  alt="">
                </div>
                <div class="right">
                  <img src="../../../assets/img/home/btn_next.png" alt="">
                </div>
          </div>
        </div>
        <audio src="../../../assets/music/Kaz - White Stallion.mp3" ref="audio" id="musicplayer" controls="controls" hidden></audio>
    </div>
</template>

<script>
export default {
    name: "HomeMusic",
    data() {
        return {
            flag: true,
            isTabFixed: false,
        }
    },
    methods: {
        itemClick() {
            this.flag = !this.flag;
            var audio = document.getElementById('musicplayer'); 
            if(this.flag) {
                audio.pause();
            } else {
                audio.play();
            }
        },
        afterLeave: function(el) {
            this.isTabFixed = true;
        },
        beforeEnter: function(el) {
          this.isTabFixed = false;
        }
    }
}
</script>

<style scoped>
.lisent {
    margin-top: 20px;
    text-align: center;
    margin-left: 5%;
    margin-right: 75%;
    padding-bottom: 5px;
    border-bottom: 3px skyblue solid;
}
.music {
    display: flex;
    padding-left: 10px;
    padding-right: 10px;
}
.music-play {
    position: relative;
    flex:1;
    margin-top: 5%;
    width: 108px;
    height: 108px;
}
.music-control {
    flex: 3;
    display: flex;
    height: 100%;
}
.music-control img {
    width: 70%;
    height: 100%;
    margin-left: 15%;
    margin-right: 15%;
}
.music-control .left, .center, .right{
    flex: 1;
    height: 50%;
    margin-top: 15%;
}
.cd {
    width: 100px;
    height: 100px;
    background: url('../../../assets/img/home/cd.png') center no-repeat;
    background-size: 100%;
    z-index: 1;

}

.cd-play-enter-active {
  animation: cd-play-in 1.5s reverse;
}
.cd-play-leave-active {
  animation: cd-play-in 1.5s ;
}
@keyframes cd-play-in {
  0% {
    transform: rotate(-10deg) ;
  }
  100% {
    transform: rotate(15deg) ;
  }
}

.music .stick {
    position: absolute;
    top: -15px;
    right: 0px;
    width: 17px;
    height: 109px;
    background: url('../../../assets/img/home/stick.png') center no-repeat;
    background-size: 100%;
    -webkit-transform-origin: 14px 21px;
    -webkit-transform: rotate(-10deg); 
    z-index: 3;
}
.music .stick1 {
    position: absolute;
    top: -15px;
    right: 0px;
    width: 17px;
    height: 109px;
    background: url('../../../assets/img/home/stick.png') center no-repeat;
    background-size: 100%;
    -webkit-transform-origin: 14px 21px;
    -webkit-transform: rotate(15deg); 
    z-index: 3;
}
.music .shine {
    position: absolute;
    display: inline-block;
    top: 85px;
    right: 35px;
    width: 1px;
    height: 1px;
    background-color: #3882ef;
    box-shadow: 0 0 16px 7px #3882ef;
    border-radius: 10px;
    z-index: 2;
    opacity: 70;
}
.kaz img{
    width:100%;
    border-radius: 10%;
}
</style>